import React, { Component } from 'react'

import {
  Flex,
  List,
  InputItem,
  Picker,
  ImagePicker,
  TextareaItem,
  Modal,
  NavBar,
  Icon
} from 'antd-mobile'

import HousePackage from '../../../components/HousePackage'

import styles from './index.module.css'

const alert = Modal.alert

// 
const roomTypeData = [
  { label: '', value: 'ROOM|d4a692e4-a177-37fd' },
  { label: '', value: 'ROOM|d1a00384-5801-d5cd' },
  { label: '', value: 'ROOM|20903ae0-c7bc-f2e2' },
  { label: '', value: 'ROOM|ce2a5daa-811d-2f49' },
  { label: '+', value: 'ROOM|2731c38c-5b19-ff7f' }
]

// 
const orientedData = [
  { label: '', value: 'ORIEN|141b98bf-1ad0-11e3' },
  { label: '', value: 'ORIEN|103fb3aa-e8b4-de0e' },
  { label: '', value: 'ORIEN|61e99445-e95e-7f37' },
  { label: '', value: 'ORIEN|caa6f80b-b764-c2df' },
  { label: '', value: 'ORIEN|dfb1b36b-e0d1-0977' },
  { label: '', value: 'ORIEN|67ac2205-7e0f-c057' },
  { label: '', value: 'ORIEN|2354e89e-3918-9cef' },
  { label: '', value: 'ORIEN|80795f1a-e32f-feb9' }
]

// 
const floorData = [
  { label: '', value: 'FLOOR|1' },
  { label: '', value: 'FLOOR|2' },
  { label: '', value: 'FLOOR|3' }
]

export default class RentAdd extends Component {
  constructor(props) {
    super(props)

    this.state = {
      // 
      tempSlides: [],

      // id
      community: {
        name: '',
        id: ''
      },
      // 
      price: '',
      // 
      size: '',
      // 
      roomType: '',
      // 
      floor: '',
      // 
      oriented: '',
      // 
      title: '',
      // 
      houseImg: '',
      // 
      supporting: '',
      // 
      description: ''
    }
  }

  // 
  onCancel = () => {
    alert('', '?', [
      {
        text: '',
        onPress: async () => this.props.history.go(-1)
      },
      {
        text: ''
      }
    ])
  }

  render() {
    const Item = List.Item
    const { history } = this.props
    const {
      community,
      price,
      size,
      roomType,
      floor,
      oriented,
      description,
      tempSlides,
      title
    } = this.state

    return (
      <div className={styles.root}>
        <NavBar
          className={styles.navHeader}
          mode="dark"
          icon={<Icon type="left" />}
          onLeftClick={this.onCancel}
        >
          
        </NavBar>
        <List
          className={styles.header}
          renderHeader={() => ''}
          data-role="rent-list"
        >
          {/*  */}
          <Item
            extra={community.name || ''}
            arrow="horizontal"
            onClick={() => history.replace('/rent/search')}
          >
            
          </Item>
          <InputItem placeholder="/" extra="/" type="number" value={price}>
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
          </InputItem>
          <InputItem placeholder="" extra="" type="number" value={size}>
            
          </InputItem>
          <Picker data={roomTypeData} value={[roomType]} cols={1}>
            <Item arrow="horizontal">
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            </Item>
          </Picker>

          <Picker data={floorData} value={[floor]} cols={1}>
            <Item arrow="horizontal"></Item>
          </Picker>
          <Picker data={orientedData} value={[oriented]} cols={1}>
            <Item arrow="horizontal">
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            </Item>
          </Picker>
        </List>

        <List
          className={styles.title}
          renderHeader={() => ''}
          data-role="rent-list"
        >
          <InputItem
            placeholder="  2 5000"
            value={title}
          />
        </List>

        <List
          className={styles.pics}
          renderHeader={() => ''}
          data-role="rent-list"
        >
          <ImagePicker
            files={tempSlides}
            multiple={true}
            className={styles.imgpicker}
          />
        </List>

        <List
          className={styles.supporting}
          renderHeader={() => ''}
          data-role="rent-list"
        >
          <HousePackage select />
        </List>

        <List
          className={styles.desc}
          renderHeader={() => ''}
          data-role="rent-list"
        >
          <TextareaItem
            rows={5}
            placeholder=""
            autoHeight
            value={description}
          />
        </List>

        <Flex className={styles.bottom}>
          <Flex.Item className={styles.cancel} onClick={this.onCancel}>
            
          </Flex.Item>
          <Flex.Item className={styles.confirm} onClick={this.addHouse}>
            
          </Flex.Item>
        </Flex>
      </div>
    )
  }
}
